.card {
  box-sizing: border-box;
  width: $card-width;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__img {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  &__title {
    font-size: 12px;
    font-weight: 500;
    line-height: 18px;
    margin-bottom: 4px;
  }

  &__product-code {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: #616070;
    margin-bottom: 16px;
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;

    &-number {
      display: flex;
      align-items: flex-end;
      text-align: right;
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: #060b35;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &-text {
      display: flex;
      align-items: flex-end;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    &-amount {
      display: flex;
      align-items: flex-end;
      text-align: right;
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: #060b35;
    }
  }

  &__button {
    display: block;
    box-sizing: border-box;
    border-radius: 5px;
    border: 1px solid #00acdc;
    padding: 11px 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    color: #fff;
    background: #00acdc;
  }

  &__button:hover {
    color: #00acdc;
    background-color: #fff;
  }
}
